'use client'

import { useState } from 'react'
import Link from 'next/link'

// Tab Configuration - Shared across Contacts module
const CONTACT_TABS = [
  { id: 'contacts', label: 'Contacts', href: '/sub-account/contacts' },
  { id: 'bulk-actions', label: 'Bulk Actions', href: '/sub-account/contacts/bulk-actions' },
  { id: 'restore', label: 'Restore', href: '/sub-account/contacts/restore' },
  { id: 'tasks', label: 'Tasks', href: '/sub-account/contacts/tasks' },
  { id: 'companies', label: 'Companies', href: '/sub-account/contacts/companies' },
  { id: 'smart-lists', label: 'Smart Lists', href: '/sub-account/contacts/smart-lists' },
  { id: 'settings', label: 'Settings', href: '/sub-account/contacts/settings' },
]

// Types
interface BulkAction {
  id: string
  name: string
  type: 'update_status' | 'add_tag' | 'remove_tag' | 'assign_user' | 'add_to_workflow' | 'send_email' | 'send_sms' | 'delete' | 'export'
  description: string
  status: 'pending' | 'processing' | 'completed' | 'failed' | 'cancelled'
  affectedContacts: number
  successCount: number
  failedCount: number
  parameters: Record<string, any>
  createdBy: string
  createdAt: string
  completedAt?: string
  errorLog?: string[]
}

interface ActionReport {
  id: string
  actionId: string
  type: string
  summary: string
  totalProcessed: number
  successCount: number
  failedCount: number
  duration: number // in seconds
  createdAt: string
}

