// Available action types for creating new bulk actions
const actionTypes = [
  { id: 'update_status', name: 'Update Status', icon: 'üîÑ', color: 'blue' },
  { id: 'add_tag', name: 'Add Tag', icon: 'üè∑Ô∏è', color: 'emerald' },
  { id: 'remove_tag', name: 'Remove Tag', icon: '‚ùå', color: 'amber' },
  { id: 'assign_user', name: 'Assign to User', icon: 'üë§', color: 'purple' },
  { id: 'add_to_workflow', name: 'Add to Workflow', icon: '‚ö°', color: 'indigo' },
  { id: 'send_email', name: 'Send Email', icon: 'üìß', color: 'cyan' },
  { id: 'send_sms', name: 'Send SMS', icon: 'üí¨', color: 'pink' },
  { id: 'export', name: 'Export Contacts', icon: 'üì•', color: 'slate' },
  { id: 'delete', name: 'Delete Contacts', icon: 'üóëÔ∏è', color: 'red' },
]

export default function BulkActionsPage() {
  const [activeTab, setActiveTab] = useState<'actions' | 'reports'>('actions')
  const [actions, setActions] = useState<BulkAction[]>(mockBulkActions)
  const [reports] = useState<ActionReport[]>(mockReports)
  const [showNewActionModal, setShowNewActionModal] = useState(false)
  const [selectedActionType, setSelectedActionType] = useState<string | null>(null)
  const [showActionDetails, setShowActionDetails] = useState<BulkAction | null>(null)
  const [filterStatus, setFilterStatus] = useState<string>('')

  // Filter actions by status
  const filteredActions = filterStatus 
    ? actions.filter(a => a.status === filterStatus)
    : actions

  // Get status badge color
  const getStatusColor = (status: string) => {
    switch (status) {
      case 'completed': return 'bg-emerald-100 text-emerald-700 border-emerald-200'
      case 'processing': return 'bg-blue-100 text-blue-700 border-blue-200 animate-pulse'
      case 'pending': return 'bg-amber-100 text-amber-700 border-amber-200'
      case 'failed': return 'bg-red-100 text-red-700 border-red-200'
      case 'cancelled': return 'bg-slate-100 text-slate-700 border-slate-200'
      default: return 'bg-slate-100 text-slate-700 border-slate-200'
    }
  }

  // Get action type icon
  const getActionTypeIcon = (type: string) => {
    const actionType = actionTypes.find(a => a.id === type)
    return actionType?.icon || 'üìã'
  }

  // Format date
  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleString('en-US', {
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    })
  }

  // Format duration
  const formatDuration = (seconds: number) => {
    if (seconds < 60) return `${seconds}s`
    const minutes = Math.floor(seconds / 60)
    const remainingSeconds = seconds % 60
    return remainingSeconds > 0 ? `${minutes}m ${remainingSeconds}s` : `${minutes}m`
  }

  // Cancel action
  const cancelAction = (actionId: string) => {
    setActions(actions.map(a => 
      a.id === actionId && (a.status === 'pending' || a.status === 'processing')
        ? { ...a, status: 'cancelled' as const }
        : a
    ))
  }

  return (
    <div className="space-y-6 pb-8">
      {/* Page Header with Tabs */}
      <div className="flex-none bg-white border-b border-slate-200 -mx-6 -mt-6 px-6 pt-6 pb-0 mb-6 rounded-t-2xl">
        <div className="flex items-center justify-between mb-4">
          <div>
            <h1 className="text-3xl font-bold bg-gradient-to-r from-violet-600 to-purple-600 bg-clip-text text-transparent">
              Contacts
            </h1>
            <p className="text-slate-600 mt-1">
              Perform actions on multiple contacts and view reports
            </p>
          </div>
          <button
            onClick={() => setShowNewActionModal(true)}
            className="px-5 py-2.5 bg-gradient-to-r from-violet-600 to-purple-600 text-white rounded-xl font-medium hover:from-violet-700 hover:to-purple-700 transition-all shadow-lg shadow-violet-500/30 hover:shadow-xl hover:shadow-violet-500/40 flex items-center gap-2 justify-center"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
            </svg>
            New Bulk Action
          </button>
        </div>
        
        {/* Tab Navigation */}
        <div className="flex items-center gap-1 overflow-x-auto pb-0 -mb-px">
          {CONTACT_TABS.map(tab => (
            <Link
              key={tab.id}
              href={tab.href}
              className={`px-4 py-3 text-sm font-medium border-b-2 transition-colors whitespace-nowrap ${
                tab.id === 'bulk-actions'
                  ? 'border-violet-600 text-violet-600'
                  : 'border-transparent text-slate-500 hover:text-slate-700 hover:border-slate-300'
              }`}
            >
              {tab.label}
            </Link>
          ))}
        </div>
      </div>

      {/* Tabs */}
      <div className="bg-white rounded-2xl shadow-sm border border-slate-200">
        <div className="border-b border-slate-200">
          <nav className="flex gap-1 px-4 pt-4">
            <button
              onClick={() => setActiveTab('actions')}
              className={`px-6 py-3 font-medium text-sm rounded-t-xl transition-all ${
                activeTab === 'actions'
                  ? 'bg-violet-50 text-violet-700 border border-slate-200 border-b-white -mb-px'
                  : 'text-slate-600 hover:text-slate-900 hover:bg-slate-50'
              }`}
            >
              <span className="flex items-center gap-2">
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 10V3L4 14h7v7l9-11h-7z" />
                </svg>
                Actions
                <span className="px-2 py-0.5 text-xs bg-violet-200 text-violet-700 rounded-full">
                  {actions.length}
                </span>
              </span>
            </button>
            <button
              onClick={() => setActiveTab('reports')}
              className={`px-6 py-3 font-medium text-sm rounded-t-xl transition-all ${
                activeTab === 'reports'
                  ? 'bg-violet-50 text-violet-700 border border-slate-200 border-b-white -mb-px'
                  : 'text-slate-600 hover:text-slate-900 hover:bg-slate-50'
              }`}
            >
              <span className="flex items-center gap-2">
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 17v-2m3 2v-4m3 4v-6m2 10H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                </svg>
                Reports
                <span className="px-2 py-0.5 text-xs bg-slate-200 text-slate-700 rounded-full">
                  {reports.length}
                </span>
              </span>
            </button>
          </nav>
        </div>

        {/* Actions Tab Content */}
        {activeTab === 'actions' && (
          <div className="p-6">
            {/* Filters */}
            <div className="flex flex-wrap gap-3 mb-6">
              <select
                value={filterStatus}
                onChange={(e) => setFilterStatus(e.target.value)}
                className="px-4 py-2 border border-slate-300 rounded-xl text-sm font-medium text-slate-700 focus:outline-none focus:ring-2 focus:ring-violet-500"
              >
                <option value="">All Status</option>
                <option value="pending">Pending</option>
                <option value="processing">Processing</option>
                <option value="completed">Completed</option>
                <option value="failed">Failed</option>
                <option value="cancelled">Cancelled</option>
              </select>
            </div>

            {/* Actions List */}
            <div className="space-y-4">
              {filteredActions.length === 0 ? (
                <div className="text-center py-12">
                  <div className="w-16 h-16 bg-slate-100 rounded-2xl flex items-center justify-center mx-auto mb-4">
                    <svg className="w-8 h-8 text-slate-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 10V3L4 14h7v7l9-11h-7z" />
                    </svg>
                  </div>
                  <h3 className="text-lg font-semibold text-slate-900 mb-2">No bulk actions found</h3>
                  <p className="text-slate-600">Create a new bulk action to get started</p>
                </div>
              ) : (
                filteredActions.map((action) => (
                  <div
                    key={action.id}
                    className="border border-slate-200 rounded-xl p-5 hover:border-violet-300 hover:shadow-md transition-all cursor-pointer"
                    onClick={() => setShowActionDetails(action)}
                  >
                    <div className="flex items-start justify-between gap-4">
                      <div className="flex items-start gap-4">
                        <div className="w-12 h-12 bg-slate-100 rounded-xl flex items-center justify-center text-2xl">
                          {getActionTypeIcon(action.type)}
                        </div>
                        <div>
                          <h3 className="font-semibold text-slate-900 mb-1">{action.name}</h3>
                          <p className="text-sm text-slate-600 mb-2">{action.description}</p>
                          <div className="flex flex-wrap items-center gap-3 text-xs text-slate-500">
                            <span className="flex items-center gap-1">
                              <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z" />
                              </svg>
                              {action.affectedContacts} contacts
                            </span>
                            <span className="flex items-center gap-1">
                              <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                              </svg>
                              {action.createdBy}
                            </span>
                            <span className="flex items-center gap-1">
                              <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                              </svg>
                              {formatDate(action.createdAt)}
                            </span>
                          </div>
                        </div>
                      </div>
                      <div className="flex flex-col items-end gap-2">
                        <span className={`px-3 py-1 text-xs font-semibold rounded-full border ${getStatusColor(action.status)}`}>
                          {action.status.charAt(0).toUpperCase() + action.status.slice(1)}
                        </span>
                        {action.status === 'completed' && (
                          <span className="text-xs text-slate-500">
                            {action.successCount}/{action.affectedContacts} success
                          </span>
                        )}
                        {action.status === 'processing' && (
                          <div className="w-24 h-2 bg-slate-200 rounded-full overflow-hidden">
                            <div 
                              className="h-full bg-blue-500 rounded-full transition-all"
                              style={{ width: `${(action.successCount / action.affectedContacts) * 100}%` }}
                            />
                          </div>
                        )}
                        {(action.status === 'pending' || action.status === 'processing') && (
                          <button
                            onClick={(e) => {
                              e.stopPropagation()
                              cancelAction(action.id)
                            }}
                            className="text-xs text-red-600 hover:text-red-700 font-medium"
                          >
                            Cancel
                          </button>
                        )}
                      </div>
                    </div>
                  </div>
                ))
              )}
            </div>
          </div>
        )}

        {/* Reports Tab Content */}
        {activeTab === 'reports' && (
          <div className="p-6">
            <div className="space-y-4">
              {reports.length === 0 ? (
                <div className="text-center py-12">
                  <div className="w-16 h-16 bg-slate-100 rounded-2xl flex items-center justify-center mx-auto mb-4">
                    <svg className="w-8 h-8 text-slate-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 17v-2m3 2v-4m3 4v-6m2 10H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                    </svg>
                  </div>
                  <h3 className="text-lg font-semibold text-slate-900 mb-2">No reports yet</h3>
                  <p className="text-slate-600">Reports will appear here after bulk actions complete</p>
                </div>
              ) : (
                reports.map((report) => (
                  <div
                    key={report.id}
                    className="border border-slate-200 rounded-xl p-5 hover:border-emerald-300 hover:shadow-md transition-all"
                  >
                    <div className="flex items-start justify-between gap-4">
                      <div className="flex items-start gap-4">
                        <div className="w-12 h-12 bg-emerald-100 rounded-xl flex items-center justify-center">
                          <svg className="w-6 h-6 text-emerald-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                          </svg>
                        </div>
                        <div>
                          <h3 className="font-semibold text-slate-900 mb-1">{report.summary}</h3>
                          <div className="flex flex-wrap items-center gap-4 text-sm">
                            <span className="text-emerald-600 font-medium">
                              {report.successCount} successful
                            </span>
                            {report.failedCount > 0 && (
                              <span className="text-red-600 font-medium">
                                {report.failedCount} failed
                              </span>
                            )}
                            <span className="text-slate-500">
                              Duration: {formatDuration(report.duration)}
                            </span>
                          </div>
                        </div>
                      </div>
                      <div className="text-right">
                        <p className="text-sm font-medium text-slate-900">
                          {((report.successCount / report.totalProcessed) * 100).toFixed(1)}%
                        </p>
                        <p className="text-xs text-slate-500">Success Rate</p>
                      </div>
                    </div>
                    <div className="mt-4 pt-4 border-t border-slate-100 flex justify-between items-center">
                      <span className="text-xs text-slate-500">{formatDate(report.createdAt)}</span>
                      <button className="text-sm text-violet-600 hover:text-violet-700 font-medium">
                        Download Report
                      </button>
                    </div>
                  </div>
                ))
              )}
            </div>
          </div>
        )}
      </div>

      {/* New Action Modal */}
      {showNewActionModal && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl max-w-2xl w-full shadow-2xl overflow-hidden max-h-[90vh] overflow-y-auto">
            <div className="bg-gradient-to-r from-violet-600 to-purple-600 px-6 py-4 flex items-center justify-between sticky top-0">
              <div className="flex items-center gap-3">
                <div className="w-10 h-10 bg-white/20 rounded-xl flex items-center justify-center backdrop-blur-sm">
                  <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 10V3L4 14h7v7l9-11h-7z" />
                  </svg>
                </div>
                <div>
                  <h3 className="text-lg font-semibold text-white">New Bulk Action</h3>
                  <p className="text-sm text-violet-100">Select an action type to perform on multiple contacts</p>
                </div>
              </div>
              <button
                onClick={() => {
                  setShowNewActionModal(false)
                  setSelectedActionType(null)
                }}
                className="text-white hover:bg-white/20 rounded-lg p-2 transition-colors"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            <div className="p-6">
              <div className="grid grid-cols-2 sm:grid-cols-3 gap-4">
                {actionTypes.map((type) => (
                  <button
                    key={type.id}
                    onClick={() => setSelectedActionType(type.id)}
                    className={`p-4 border-2 rounded-xl text-center hover:border-violet-400 hover:bg-violet-50 transition-all ${
                      selectedActionType === type.id
                        ? 'border-violet-500 bg-violet-50'
                        : 'border-slate-200'
                    }`}
                  >
                    <span className="text-3xl mb-2 block">{type.icon}</span>
                    <span className="text-sm font-medium text-slate-700">{type.name}</span>
                  </button>
                ))}
              </div>

              {selectedActionType && (
                <div className="mt-6 pt-6 border-t border-slate-200">
                  <p className="text-sm text-slate-600 mb-4">
                    Configure your {actionTypes.find(a => a.id === selectedActionType)?.name} action below.
                    This feature will be fully implemented in the next update.
                  </p>
                  <div className="flex gap-3">
                    <button
                      onClick={() => {
                        setShowNewActionModal(false)
                        setSelectedActionType(null)
                      }}
                      className="flex-1 px-4 py-3 border border-slate-300 text-slate-700 rounded-xl hover:bg-slate-50 font-medium"
                    >
                      Cancel
                    </button>
                    <button
                      className="flex-1 px-4 py-3 bg-gradient-to-r from-violet-600 to-purple-600 text-white rounded-xl font-medium hover:from-violet-700 hover:to-purple-700 transition-all shadow-lg"
                    >
                      Continue Setup
                    </button>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      )}

      {/* Action Details Modal */}
      {showActionDetails && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl max-w-lg w-full shadow-2xl overflow-hidden">
            <div className={`px-6 py-4 flex items-center justify-between ${
              showActionDetails.status === 'completed' ? 'bg-gradient-to-r from-emerald-600 to-teal-600' :
              showActionDetails.status === 'failed' ? 'bg-gradient-to-r from-red-600 to-rose-600' :
              showActionDetails.status === 'processing' ? 'bg-gradient-to-r from-blue-600 to-cyan-600' :
              'bg-gradient-to-r from-slate-600 to-slate-700'
            }`}>
              <div className="flex items-center gap-3">
                <div className="w-10 h-10 bg-white/20 rounded-xl flex items-center justify-center text-2xl backdrop-blur-sm">
                  {getActionTypeIcon(showActionDetails.type)}
                </div>
                <div>
                  <h3 className="text-lg font-semibold text-white">{showActionDetails.name}</h3>
                  <p className="text-sm text-white/80 capitalize">{showActionDetails.status}</p>
                </div>
              </div>
              <button
                onClick={() => setShowActionDetails(null)}
                className="text-white hover:bg-white/20 rounded-lg p-2 transition-colors"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            <div className="p-6 space-y-4">
              <p className="text-slate-600">{showActionDetails.description}</p>

              <div className="grid grid-cols-2 gap-4">
                <div className="bg-slate-50 rounded-xl p-4">
                  <p className="text-sm text-slate-600">Total Contacts</p>
                  <p className="text-2xl font-bold text-slate-900">{showActionDetails.affectedContacts}</p>
                </div>
                <div className="bg-emerald-50 rounded-xl p-4">
                  <p className="text-sm text-emerald-600">Successful</p>
                  <p className="text-2xl font-bold text-emerald-700">{showActionDetails.successCount}</p>
                </div>
              </div>

              {showActionDetails.failedCount > 0 && (
                <div className="bg-red-50 rounded-xl p-4">
                  <p className="text-sm text-red-600 mb-1">Failed: {showActionDetails.failedCount}</p>
                  {showActionDetails.errorLog && (
                    <ul className="text-xs text-red-600 space-y-1">
                      {showActionDetails.errorLog.map((error, i) => (
                        <li key={i}>‚Ä¢ {error}</li>
                      ))}
                    </ul>
                  )}
                </div>
              )}

              <div className="text-sm text-slate-600 space-y-1">
                <p>Created by: <span className="font-medium text-slate-900">{showActionDetails.createdBy}</span></p>
                <p>Started: <span className="font-medium text-slate-900">{formatDate(showActionDetails.createdAt)}</span></p>
                {showActionDetails.completedAt && (
                  <p>Completed: <span className="font-medium text-slate-900">{formatDate(showActionDetails.completedAt)}</span></p>
                )}
              </div>

              <button
                onClick={() => setShowActionDetails(null)}
                className="w-full px-4 py-3 border border-slate-300 text-slate-700 rounded-xl hover:bg-slate-50 font-medium"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
